import getJSON from '../../components/utils/getJSON';

export var OUTLOOK_INIT = 'OUTLOOK_INIT';
export var OUTLOOK_FETCH_FOLDER = 'OUTLOOK_FETCH_FOLDER';
export var OUTLOOK_FETCH_MESSAGE = 'OUTLOOK_FETCH_MESSAGE';

export function outlookInit() {
  return function (dispatch) {
    return getJSON('assets/api/outlook/outlook.json').then(function (res) {
      dispatch({
        type: OUTLOOK_INIT,
        data: res.data
      });
    });
  };
}

export function outlookFetchFolder(folder) {
  return function (dispatch) {
    return getJSON('assets/api/outlook/' + folder + '.json').then(function (res) {
      dispatch({
        type: OUTLOOK_FETCH_FOLDER,
        data: res.data,
        key: folder
      });
    });
  };
}

export function outlookFetchMeesage(id) {
  return function (dispatch) {
    return getJSON('assets/api/outlook/message.json').then(function (res) {
      dispatch({
        type: OUTLOOK_FETCH_MESSAGE,
        data: res.data,
        key: id
      });
    });
  };
}
