export var CHAT_INIT = 'CHAT_INIT';
export var CHAT_MESSAGE_TO = 'CHAT_MESSAGE_TO';
export var CHAT_MESSAGE_UPDATE = 'CHAT_MESSAGE_UPDATE';
export var CHAT_MESSAGE_SEND = 'CHAT_MESSAGE_SEND';

export function chatInit() {
  return function (dispatch) {
    return $.getJSON('assets/api/chat/chat.json').then(function (data) {
      dispatch({
        type: CHAT_INIT,
        data: data
      });
    });
  };
}

export function messageTo(user) {
  return {
    type: CHAT_MESSAGE_TO,
    user: user
  };
}

export function messageUpdate() {
  return {
    type: CHAT_MESSAGE_UPDATE
  };
}

export function messageSend(message) {
  return function (dispatch, getState) {
    return dispatch({
      type: CHAT_MESSAGE_SEND,
      user: getState().user,
      message: message
    });
  };
}
